import React from 'react'
import { Container, Row, Col, Form } from 'react-bootstrap'
import {
    Button,
    FormControl,
    FormLabel,
    FormErrorMessage,
    FormHelperText,
    Select,
    Input
} from "@chakra-ui/react"
import { useForm } from '../hooks/useForm.jsx'
import { useDispatch } from 'react-redux'
import { actionLicor } from '../actions/ActionCarrito.js'
import uuid from 'react-uuid'

const AgregarProducto = () => {
    const [formValue, handleInputChange, reset] = useForm({
        nombre: '',
        producto: '',
    })

    const dispatch = useDispatch();
    const AgregarProducto = (producto) => {
        dispatch(actionLicor(producto))
    }

    const { nombre, producto } = formValue;

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log("funcina");
        // reset();
        // console.log("funciona");

        // AgregarProducto = ({
        //     id: uuid()
        // })

        AgregarProducto = ({
            id: uuid(),
            nombre,
            producto
        })
    }
    return (
        <React.Fragment>
            <Container>
                <Row>
                    <Col xs={6}>
                        {/* <FormControl onSubmit = {handleSubmit}>
                            <h1>Realice sus Compras Aqui</h1>
                            <FormLabel>Ingrese su Nombre</FormLabel>
                            <Input type="text" name = 'nombre' value = {nombre} onChange = {handleInputChange}/>
                            <FormHelperText>Ocupe en el campo su nombre completo</FormHelperText>
                            <FormLabel>Producto Deseado</FormLabel>
                            <Input type="text"  name = 'producto' value = {producto} onChange = {handleInputChange}/>
                            <FormHelperText>Escriba el nombre del Producto</FormHelperText>
                            <Button colorScheme="blue" type = "submit">Button</Button>
                        </FormControl> */}
                        <Form onSubmit = {handleSubmit}>
                            <h1>Ordene sus Licores Aqui</h1>
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Ingrese su Nombre</Form.Label>
                                <Form.Control type="text" placeholder="Enter email" name = 'nombre' value = {nombre} onChange = {handleInputChange}/>
                                <Form.Text className="text-muted">
                                    We'll never share your email with anyone else.
                                </Form.Text>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicPassword">
                                <Form.Label>Producto Deseado</Form.Label>
                                <Form.Control type="text" placeholder="Password" name = 'producto' value = {producto} onChange = {handleInputChange}/>
                            </Form.Group>
                            <Button variant="primary" type="submit">
                                Submit
                            </Button>
                        </Form>
                    </Col>
                    <Col xs={6}>
                        <div className="card mt-5">
                            <div className="card-body">
                                <h2 >Carrito de Compras</h2>
                                <div className="lista-citas">
                                    <table className="table">
                                        <thead>
                                            <tr>
                                                <th scope="col">Nombre</th>
                                                <th scope="col">Fecha</th>
                                                <th scope="col">Hora</th>
                                                <th scope="col">Fractura</th>
                                                <th scope="col">Acci√≥n</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        </tbody>
                                    </table>
                                </div>
                            </div >
                        </div >
                    </Col>
                </Row>
            </Container>
        </React.Fragment>

    )
}

export default AgregarProducto
